export default function Throttle(fn, delay) {
    var last, deferTime
    return function(args) {
        var that = this
        var now = +new Date()
        // fnlastlast
        if(last && now < last + delay) {
            clearTimeout(deferTime)
            deferTime = setTimeout(function() {
                last = now
                fn.call(that, args)
            }, delay)
        } else {
            // lastfnlast
            last = now
            fn.call(that, args)
        }
    }
}


